@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: 'DMSans';
  src: url('./fonts/DMSans-VariableFont_opsz\,wght.ttf');
}

body {
  margin: 0;
  padding: 0;
  font-size: 14px;
  box-sizing: border-box;
  font-style: normal;
  font-weight: 400;
  scroll-behavior: smooth;
  background-color: #04161A;
}

* {
  font-family: 'DMSans';
  box-sizing: border-box;
}

*::-webkit-scrollbar {
  display: none;
}


@layer utilities {
  /* Hide scrollbar for Chrome, Safari and Opera */
  .no-scrollbar::-webkit-scrollbar {
    display: none;
  }
  /* Hide scrollbar for IE, Edge and Firefox */
  .no-scrollbar {
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
  }
}
@layer components {
  .container {
    @apply md:max-w-[1223px] w-full mx-auto px-2 pl-[17px] xl:pl-0 pr-[17px] xl:pr-0;
  }
}

.bg-welcome {
  /* border-radius: 10px; */
  background: linear-gradient(180deg, #052223 0.92%, #041A1B 97.53%), #041A1B;
}
.me{
  background-color: rgba(38, 37, 37, 0.299);
}
